<template>
  <div>
    <div class="overline mt-4 ml-4">Registro</div>
    <v-form 
        ref="form" 
        v-model="valid"
        lazy-validation
    >
      <v-container>
        <v-row>
          <v-col cols="12">
            <v-text-field
                v-model="username"
                label="Email"
                outlined
                dense
                required
                @keyup.enter="submit"
                ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12">
            <v-text-field
                v-model="password"
                label="Password"
                outlined
                dense
                required
                @keyup.enter="submit"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12">
            <v-text-field
                v-model="password2"
                label="Repeat Password"
                outlined
                dense
                required
                @keyup.enter="submit"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12">
            <v-text-field
                v-model="fistName"
                label="First Name"
                outlined
                dense
                required
                @keyup.enter="submit"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12">
            <v-text-field
                v-model="lastName"
                label="Last Name"
                outlined
                dense
                required
                @keyup.enter="submit"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12">
            <v-text-field
                v-model="age"
                label="Age"
                type="number"
                outlined
                dense
                required
                @keyup.enter="submit"
            ></v-text-field>
          </v-col>
        </v-row>
      </v-container>
    <v-card-actions>
      <v-btn v-on:click="submit" text>SingUp</v-btn>
    </v-card-actions>
    </v-form>
  </div>
</template>
<script>
export default {
  data: () => ({
    username: "",
    emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
    password: "",
    email: "",
    fistName: "",
    lastName: "",
    age: ""
  }),
  methods: {
    submit() {
        alert(this.username + " " + this.password + " " + this.email + " " + this.fistName + " " + this.lastName + " " + this.age)
    },
    validate () {
        if (this.$refs.form.validate()) {
          this.snackbar = true
        }
      },
  },
  computed: {}
};
</script>
